import React, { useContext, useState } from 'react'
import { Container, Form, Card, Button, Alert } from 'react-bootstrap'
import { NavLink, useHistory, useLocation } from 'react-router-dom'
import { LOGIN_ROUTE, REGISTRATION_ROUTE, SHOP_ROUTE } from '../utils/consts'
import { login, registration } from '../http/userAPI'
import { observer } from 'mobx-react-lite'
import { Context } from '../index'

const Auth = observer(() => {
	const { user } = useContext(Context)
	const location = useLocation()
	const history = useHistory()
	const isLogin = location.pathname === LOGIN_ROUTE
	const [email, setEmail] = useState('letstest@it.go')
	const [password, setPassword] = useState('letstest@it.go')

	const click = async () => {
		try {
			let data
			if (isLogin) {
				data = await login(email, password)
			} else {
				data = await registration(email, password)
			}
			user.setUser(user)
			user.setIsAuth(true)
			history.push(SHOP_ROUTE)
		} catch (e) {
			alert(e.response.data.message)
		}
	}

	return (
		<Container
			className="d-flex justify-content-center align-items-center"
			style={{ height: window.innerHeight - 54 }}
		>
			<Card style={{ width: 600 }} className="p-5">
				<h2 className="m-auto">{isLogin ? 'Sign In' : 'Sign Up'}</h2>
				<Form className="d-flex flex-column">
					<Form.Control
						className="mt-3"
						placeholder="Enter your e-mail"
						type="email"
						value={email}
						onChange={e => setEmail(e.target.value)}
					/>
					<Form.Control
						className="mt-3"
						placeholder="Enter your password"
						value={password}
						type="password"
						onChange={e => setPassword(e.target.value)}
					/>
					<div className="d-flex justify-content-between mt-3 align-items-center">
						{isLogin ? (
							<div className="d-flex flex-column">
								<span>Still have no acc? </span>
								<NavLink to={REGISTRATION_ROUTE}>Sign Up</NavLink>
							</div>
						) : (
							<div className="d-flex flex-column">
								<span>Do you already have an acc? </span>
								<NavLink to={LOGIN_ROUTE}>Sign In</NavLink>
							</div>
						)}
						<Button onClick={click} variant={'outline-success'}>
							{isLogin ? 'Sign In' : 'Sign Up'}
						</Button>
					</div>
				</Form>
			</Card>
		</Container>
	)
})

export default Auth
